<template>
  <div id="app">
    <leftNav></leftNav>
    <div class="main">
      <router-view/>
    </div>
  </div>
</template>

<script>
import leftNav from '@/components/leftNav'
export default {
  name: 'App',
  components: {
    leftNav
  },
  created(){
    let that = this
    // window.addEventListener("load", function (event) {
      if(localStorage.getItem('pos_user')){
        let user = JSON.parse(localStorage.getItem('pos_user'))
        that.$store.commit('setUserAccount', user.account)
        that.$store.commit('setUserName', user.name)
      }
    // });
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

</style>



