<template>
    <div class="fixed_center">
        <el-form label-position="right" label-width="80px" ref="ruleForm" :model="formData" :rules="rules">
            <el-form-item label="用户名" prop="name">
                <el-input v-model="formData.user"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="pwd">
                <el-input v-model="formData.pwd"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button>取消</el-button>
                <el-button type="primary" @click="onSubmit('ruleForm')">登录</el-button>
                <el-button type="primary" @click="onAddSubmit('ruleForm')">注册</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
export default {
    name:'login',
    data(){
        return {
            formData: {
                user: '',
                pwd: ''
            },
             rules: {
                user: [
                    { required: true, message: '请输入用户名', trigger: 'blur' }
                ],
                 pwd: [
                    { required: true, message: '请输入密码', trigger: 'blur' }
                ],
             }
        }
    },
    methods:{
        onSubmit(formName){
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$api.loginApi.getUser(this.formData,(res)=>{
                        if(res.code === 200){
                        this.resetSetItem('pos_user', JSON.stringify({
                            account: res.data[0].userAccount, 
                            name: res.data[0].userName,
                            userImg: res.data[0].userImg
                            }))
                            this.$store.commit('setUserAccount', res.data[0].userAccount)
                        this.$store.commit('setUserName', res.data[0].userName)
                        this.$router.push({
                            path:'/money'
                        })
                            this.$message.success('您好 ' + (res.data[0].userName || res.data[0].userAccount) +' ，欢迎上线');
                    }else{
                        this.$message.error(res.msg);
                    }
                    }),(error)=>{
                        this.$message.error(error.msg);
                        console.log(error)
                    }
                }
            });
        },
        onAddSubmit(formName){
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$api.loginApi.addUser(this.formData,(res)=>{
                    if(res.code === 200){
                        this.resetSetItem('pos_user', JSON.stringify({ account:this.formData.user,name:''}))
                        this.$store.commit('setUserAccount', this.formData.user)
                        this.$store.commit('setUserName', '')
                        this.$router.push({
                            path:'/money'
                        })
                        this.$message.success('您好 '+ this.formData.user +' ，欢迎上线');
                    }else{
                        this.$message.error(res.msg);
                    }
                    }),(error)=>{
                        this.$message.error(error.msg);
                        console.log(error)
                    }
                }
            });
        }
    }
}
</script>
<style scoped>
</style>


