import * as http from '@/api/http.js'

const vipApi = {
    // 查询vip
    getVipList(data,callBack,error){
        return http.get('/vip/getVipList',data).then(callBack).catch(error)
    },
    // 新建vip
    addVipUser(data, callBack, error) {
        return http.post('/vip/addVipUser', data).then(callBack).catch(error)
    },
    // 查询vip记录
    getVipFoodsList(data, callBack, error) {
        return http.get('/vip/getVipFoodsList', data).then(callBack).catch(error)
    },
    // 更新vip购买记录
    addVipFoodsList(data, callBack, error) {
        return http.post('/vip/addVipFoodsList', data).then(callBack).catch(error)
    },
}

export default vipApi


