<template>
    <div class="poscfig">
        <el-row>
            <el-col :span="20" :offset="1">
                <div style="margin-bottom:15px">
                    用户名：
                    <el-input v-model="userFrom.name" style="width:300px"></el-input>
                </div>
                <div>
                   <p style="margin-bottom: 15px;">用户头像：</p> 
                    <el-upload class="avatar-uploader" 
                    action="" :show-file-list="false" 
                    :http-request="handlePictureCardPreview">
                        <img v-if="userFrom.userImg" :src="userFrom.userImg" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </div>
            
                <div style="margin-top:10px;">
                    <el-row>
                        <el-button type="primary" round size="small" @click="submitbtn">保存设置</el-button>
                    </el-row>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    name:'setting',
    data(){
        return{
            userFrom:{
                name: this.$store.state.userName,
                account: this.$store.state.userAccount,
                userImg:''
            }
        }
    },
    created () {
        let user = JSON.parse(localStorage.getItem('pos_user'))
        this.userFrom = {...user}
        console.log(this.userFrom);
    },
    methods:{
        submitbtn(){
            this.$api.loginApi.editUser(this.userFrom, (res) => {
                if (res.code === 200) {
                    this.resetSetItem('pos_user', JSON.stringify({
                        account: this.userFrom.account,
                        name: this.userFrom.name,
                        userImg: this.userFrom.userImg
                    }))
                    this.$store.commit('setUserAccount', this.userFrom.account)
                    this.$store.commit('setUserName', this.userFrom.name)
                    this.$message.success('更新成功');
                } else {
                    this.$message.error(res.msg);
                }
            }), (error) => {
                this.$message.error(error.msg);
                console.log(error)
            }
        },
        handlePictureCardPreview(file) {
            var fileReader = new FileReader()
            fileReader.readAsDataURL(file.file)
            fileReader.onload = (e) => {
                this.dealImage(e.target.result, (val) => {
                    console.log(val);
                    this.userFrom.userImg = val
                }) //获取base64字符串
            }
        },
        dealImage(base64, callback) {
            // 压缩
            var newImage = new Image();
            var quality = 0.6;    //压缩系数0-1之间
            newImage.src = base64;
            newImage.setAttribute("crossOrigin", 'Anonymous');	//url为外域时需要
            var imgWidth, imgHeight;
            newImage.onload = function () {
                imgWidth = this.width;
                imgHeight = this.height;
                var canvas = document.createElement("canvas");
                var ctx = canvas.getContext("2d");
                if (Math.max(imgWidth, imgHeight) > 120) {
                    if (imgWidth > imgHeight) {
                        canvas.width = 120; // 设置压缩宽度大小
                        canvas.height = 120 * imgHeight / imgWidth;
                    } else {
                        canvas.height = 120;
                        canvas.width = 120 * imgWidth / imgHeight;
                    }
                } else {
                    canvas.width = imgWidth;
                    canvas.height = imgHeight;
                    quality = 0.6;
                }
                ctx.clearRect(0, 0, canvas.width, canvas.height);
                ctx.drawImage(this, 0, 0, canvas.width, canvas.height);
                var base64 = canvas.toDataURL("image/jpeg", quality); //压缩语句
                // 如想确保图片压缩到自己想要的尺寸,如要求在5-10kb之间，请加以下语句，quality初始值根据情况自定
                while (base64.length / 1024 > 10) {
                    quality -= 0.01;
                    base64 = canvas.toDataURL("image/jpeg", quality);
                }
                // 防止最后一次压缩低于最低尺寸，只要quality递减合理，无需考虑
                while (base64.length / 1024 < 5) {
                    quality += 0.001;
                    base64 = canvas.toDataURL("image/jpeg", quality);
                }
                callback(base64);//必须通过回调函数返回，否则无法及时拿到该值
            }
        },
    }
}
</script>

<style scoped>
.poscfig{
    padding-top: 20px;
    text-align: left;
}
</style>


