import Vue from 'vue'
import axios from 'axios'
import QS from 'qs'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios);
const service = axios.create({
    // baseURL: process.env.BASE_API, // api 的 base_url
    timeout: 5000, // 请求超时时间
    baseURL: process.env.API,
})

//http request 拦截器
// service.interceptors.request.use(
//     config => {
//         // config.data = JSON.stringify(config.data);
//         config.headers = {
//             'Content-Type': 'application/x-www-form-urlencoded',
//             'X-Requested-With': 'XMLHttpRequest'
//         }
//         config.cancelToken = new axios.CancelToken(function (cancel) {
//             store.commit('pushToken', cancel)
//         })
//         return config;
//     },
//     error => {
//         console.log(error) // for debug
//         return Promise.reject(err);
//     }
// );

// 响应拦截器
service.interceptors.response.use(
    response => {
        if (response.status == 200) {
            return response
        } else {
            return Promise.reject('error')
        }
    },error => {
        console.log('error' + error) // for debug error.response.status
        if (service.isCancel(error)) {
            // 为了终结promise链 就是实际请求 不会走到.catch(rej=>{});这样就不会触发错误提示之类了。
            return new Promise(() => { });
        } else {
            return Promise.reject(error.response)
        }
        //return Promise.reject(error.response)
    }
)

export default service;



/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */

export function get(url, params = {}) {
    return new Promise((resolve, reject) => {
        service.get(url, {
            params: params
        }).then(response => {
            resolve(response.data);
        }).catch(err => {
            reject(err)
        })
    })
}

/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function post(url, data = {}) {
    return new Promise((resolve, reject) => {
        service.post(url, QS.stringify(data))
            .then(response => {
                resolve(response.data);
            }, err => {
                reject(err)
            })
    })
}

export function postBody(url, data = {}) {
    return new Promise((resolve, reject) => {
        service.post(url, JSON.stringify(data), {
            headers: {
                "Content-Type": "application/json;charset=utf-8"  //头部信息
            }
        }).then(response => {
            resolve(response.data);
        }, err => {
            reject(err)
        })
    })
}



