import * as http from '@/api/http.js'

const loginApi = {
    // 查询登录用户
    getUser(data,callBack,error){
        return http.post('/login/getUser',data).then(callBack).catch(error)
    },
    // 新建用户
    addUser(data, callBack, error) {
        return http.post('/login/addUser', data).then(callBack).catch(error)
    },
    // 修改用户信息
    editUser(data, callBack, error) {
        return http.post('/login/editUser', data).then(callBack).catch(error)
    },
}

export default loginApi


