<template>
    <div class="left-nav">
       
        <ul class="cookList">
            <li
            v-for='(good, index) in fooddata1'
            :key='index'>
                <span class="deleted" @click="deleteds(good.foodsId)">X</span>
                <span class="oftenFoods" :style="{ color: good.oftenFoods === '1' ? '#fab72b' : ''}"
                 @click="setOfter({ id: good.foodsId, oftenFoods:good.oftenFoods})">
                    <i class="el-icon-star-on"></i>
                </span>
                <span class='foodImg'
                    ><img :src='good.foodsImg || defaultImg' width='100%'
                /></span>
                <span class='foodName'>{{ good.foodsName }}</span>
                <span class='foodPrice'>￥{{ good.foodsPrice }}元</span>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    props:['fooddata','foodindex'],
    data (){
        return {
            fooddata1:this.fooddata,
            defaultImg: require('../assets/img/未知.png')
        }
    },
    methods:{
        deleteds (val){
            this.$emit('deletedclick', val)
        },
        setOfter(val) {
            this.$emit('setOfterclick', val)
        }
    },
    watch:{
        fooddata: {
    　　　　handler(newValue) {
        　　　　this.fooddata1 = newValue
    　　　　　　/* for (let i = 0; i < newValue.length; i++) {
    　　　　　　　　if (oldValue[i] != newValue[i]) {
    　　　　　　　　}
    　　　　　　} */
    　　　　},
    　　　　deep: true
    　　}
    }
}
</script>
<style scoped>
.cookList li {
  list-style: none;
  width: 23%;
  border: 1px solid #e5e9f2;
  height: auot;
  overflow: hidden;
  background-color: #fff;
  padding: 2px;
  float: left;
  margin: 2px;
  cursor: pointer;
  position: relative;
}
.cookList li span {
  display: flex;
  float: left;
  line-height: 16px;
}
.foodImg {
  width: 40%;
  height: 80px;
}
.foodName {
  font-size: 16px;
  padding-left: 10px;
  color: brown;
  width: 50%;
  text-align: left;
  height: 30px;
  overflow: hidden;
}
.foodPrice {
  font-size: 16px;
  padding-left: 10px;
  padding-top: 10px;
}
.deleted{
    position: absolute;
    width: 20px;
    cursor: pointer;
    right: 0;
    color: red;
    background: #fff;
}
.oftenFoods{
    position: absolute;
    width: 20px;
    cursor: pointer;
    right: 30px;
}
</style>



