<template>
  <div class='pos'>
    <el-row>
      <el-col :span='10' class='pos-order' id='order'>
        <el-tabs v-model="activeName">
          <el-tab-pane label='点餐' name='dainc'>
            <el-table stripe border :data='tableData' style='width: 100%'>
              <el-table-column prop='foodsName' label='商品名称'>
              </el-table-column>
              <el-table-column prop='count' label='数量' width='70'>
              </el-table-column>
              <el-table-column prop='foodsPrice' label='金额' width='70'>
              </el-table-column>
              <el-table-column label='操作' width='120' fixed='right'>
                <template slot-scope='scope'>
                  <el-button type='text' size='small' @click.native.prevent='reduce(scope.$index, tableData)'>删除
                  </el-button>
                  <el-button type='text' size='small' @click.native.prevent='add(scope.$index, tableData)'>添加
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class='totalDiv'>
              <span>数量：</span>{{ totalNum }} <span>金额：</span>{{ totalMoney }}元
            </div>
            <div style='margin-top: 20px'>
              <el-button type='warning' @click="guadan">挂单</el-button>
              <el-button type='danger' @click="reduceAll">删除</el-button>
              <el-button type='success' @click="successAll()">结账</el-button>
              <el-button type='success' @click="dialogFormVisible = true">会员结账</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label='挂单' name='guad'>
            <el-table stripe border :data='tableDatas' style='width: 100%'>
              <el-table-column prop='foodsName' label='商品名称'>
              </el-table-column>
              <el-table-column prop='count' label='数量' width='70'>
              </el-table-column>
              <el-table-column prop='foodsPrice' label='金额' width='70'>
              </el-table-column>
              <el-table-column label='操作' width='120' fixed='right'>
                <template>
                  <el-button type='text' size='small' disabled>删除</el-button>
                  <el-button type='text' size='small' disabled>添加</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class='totalDiv'>
              <span>数量：</span>{{ totalNums }} <span>金额：</span>{{ totalMoneys }}元
            </div>
            <div style='margin-top: 20px'>
              <el-button type='warning' @click="qxguadan">取消挂单</el-button>
              <el-button type='danger' @click="reduceAlls">删除</el-button>
            </div>
          </el-tab-pane>
          <!-- <el-tab-pane label='外卖' name='waim'>外卖</el-tab-pane> -->
        </el-tabs>
      </el-col>
      <el-col :span='14'>
        <div class='often-goods'>
          <div class='title'>常用商品</div>
          <div class='often-goods-list'>
            <ul>
              <li v-for='(good, index) in oftenGoods' :key='index' @click='addothenList(good)'>
                <span>{{ good.foodsName }}</span>
                <span class='o-price'>￥{{ good.foodsPrice }}元</span>
              </li>
            </ul>
          </div>
        </div>

        <div class='goods-type'>
          <el-tabs v-model="activeName1" @tab-click="handleClick">
            <template v-for="item in foodType">
              <el-tab-pane :label='item.foodTypeName' :name="item.foodTypeId" :key="item.foodTypeId">
                <div>
                  <ul class='cookList'>
                    <li v-for='(good, index) in foodsMap[item.foodTypeId]' :key='index' @click='addothenList(good)'>
                      <span class='foodImg'><img :src='good.foodsImg || defaultImg' width='100%' /></span>
                      <span class='foodName'>{{ good.foodsName }}</span>
                      <span class='foodPrice'>￥{{ good.foodsPrice }}元</span>
                    </li>
                  </ul>
                </div>
              </el-tab-pane>
            </template>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
    <!-- 添加 -->
    <el-dialog title="会员号" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label="会员编号" :label-width="formLabelWidth">
          <el-input v-model="form.vipId" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="successAll('vip')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'money',
  data() {
    return {
      activeName: 'dainc',
      activeName1: '',
      totalNum: 0, //点餐数量
      totalNums: 0,//挂单点餐数量
      totalMoney: 0,//点餐总额
      totalMoneys: 0,//挂单点餐总额
      tableData: [],
      tableDatas: [],
      oftenGoods: [],
      foodsMap: {},
      dialogFormVisible: false,//弹窗所需项
      formLabelWidth: '120px',//弹窗所需项
      form: {
        vipId: null,
      },//vip号
      foodType: [],
      defaultImg: require('../assets/img/未知.png')
    }
  },
  created: function () {
    this.getFoodsType()
    this.getOftenFoods()
  },
  mounted: function () {
    var orderHeight = document.body.clientHeight
    document.querySelector('#order').style.height = orderHeight + 'px'
  },
  methods: {
    getOftenFoods() {
      // 常用商品
      this.$api.foodApi.getOftenFoods({user: this.$store.state.userAccount}, (res) => {
        if (res.code === 200) {
          this.oftenGoods = res.data
        } else {
          this.oftenGoods = []
        }
      })
    },
    getFoodsType() {
      // 食品分类
      this.$api.foodApi.getFoodsType({ user: this.$store.state.userAccount }, (res) => {
        if (res.code === 200) {
          this.foodType = res.data
          this.foodType.forEach(item => {
            item.foodTypeId += ''
          })
          res.data.map(item => item.foodTypeId).forEach((item) => {
            if (!this.foodsMap[item]) {
              this.$set(this.foodsMap, item + '', [])
            }
            // this.foodsMap[item] = []
          })
          let activeName1 = this.foodType[0].foodTypeId + ''
          this.getFoodsList(activeName1)
          this.activeName1 = activeName1
        } else {
          this.foodType = []
        }
      })
    },
    getFoodsList(type) {
      // 查询商品
      this.$api.foodApi.getFoodsList({ type }, (res) => {
        if (res.code === 200) {
          this.foodsMap[type] = res.data
        } else {
          this.foodsMap[type] = []
        }
      })
    },
    handleClick(tab, event) {
      // console.log(tab, event);
      this.getFoodsList(tab.name)
    },

    addothenList(goods) {
      let isHave = false
      for (let i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].foodsId === goods.foodsId) {
          isHave = true
          break
        }
      }

      // 存在商品
      if (isHave) {
        let arr = this.tableData.filter((o) => o.foodsId === goods.foodsId)
        arr[0].count++
      } else {
        // 不存在
        let newgood = {
          ...goods,
          count: 1
        }
        this.tableData.push(newgood)
      }
      this.changetotal()
    },
    changetotal() {
      this.totalNum = 0
      this.totalMoney = 0
      for (let i = 0; i < this.tableData.length; i++) {
        this.totalNum += this.tableData[i].count
        this.totalMoney += this.tableData[i].foodsPrice * this.tableData[i].count
      }
    },
    add (index, data) {
      this.$set(this.tableData[index], 'count', ++this.tableData[index].count)
      this.changetotal()
    },
    reduce (index, data) {
      this.$set(this.tableData[index], 'count', --this.tableData[index].count)
      if (this.tableData[index].count === 0) {
        this.tableData = this.tableData.filter((o) => o.foodsId !== this.tableData[index].foodsId)
      }
      this.changetotal()
    },
    reduceAll () {
      this.tableData = []
      this.totalNum = 0
      this.totalMoney = 0
    },
    reduceAlls () {
      this.tableDatas = []
      this.totalNums = 0
      this.totalMoneys = 0
    },
    successAll (type) {
      let that = this;
      that.tableData.forEach(item => item.foodsImg = '')
      let obj = {
        vipId: type ? that.form.vipId : '1',
        foodlist: JSON.stringify(that.tableData),
        price: that.totalMoney,
        times: new Date().getTime(),
        user: this.$store.state.userAccount 
      }
      that.$api.vipApi.addVipFoodsList(obj, (res) => {
        if (res.code === 200) {
          that.$message({
            message: '结账成功，感谢你又为店里贡献了' + that.totalMoney + '元!',
            type: 'success',
          })
          that.tableData = []
          that.totalNum = 0
          that.totalMoney = 0
          that.dialogFormVisible = false
        } else {
          that.$message({
            message: res.msg,
            type: 'warning',
          })
        }
      })
    },
    guadan () {
      this.tableDatas = this.tableData
      this.totalNums = this.totalNum
      this.totalMoneys = this.totalMoney
      this.activeName = 'guad'
      this.tableData = []
      this.totalNum = 0
      this.totalMoney = 0
    },
    qxguadan () {
      this.tableData = this.tableDatas
      this.totalNum = this.totalNums
      this.totalMoney = this.totalMoneys
      this.activeName = 'dainc'
      this.tableDatas = []
      this.totalNums = 0
      this.totalMoneys = 0
    }
  }
}
</script>

<style>
.pos-order {
  background-color: #f9fafc;
  border-right: 1px solid #c0ccda;
}

.el-tabs--top .el-tabs__item.is-top:nth-child(2) {
  padding-left: 20px;
}

.title {
  height: 20px;
  border-bottom: 1px solid #d3dce6;
  background-color: #f9fafc;
  padding: 10px;
  text-align: left;
}

.often-goods-list li {
  list-style: none;
  float: left;
  border: 1px solid #8baef5;
  padding: 10px;
  margin: 10px;
  background-color: #fff;
  border-radius: 5px;
}

.o-price {
  color: #8baef5;
}

.goods-type {
  clear: both;
}

.cookList li {
  list-style: none;
  width: 23%;
  border: 1px solid #e5e9f2;
  height: 60px;
  overflow: hidden;
  background-color: #fff;
  padding: 2px;
  float: left;
  margin: 2px;
  cursor: pointer;
}

.cookList li span {
  display: flex;
  float: left;
  line-height: 16px;
}

.foodImg {
  width: 40%;
  height: 60px;
}

.foodName {
  font-size: 16px;
  padding-left: 10px;
  color: brown;
  width: 50%;
  text-align: left;
  height: 30px;
  overflow: hidden;
}

.foodPrice {
  font-size: 16px;
  padding-left: 10px;
  padding-top: 10px;
}
</style>



