<template>
    <div>
        <el-row>
            <el-button type="primary" round @click="addFoodTypeVisible = true">添加分类</el-button>
            <el-button type="primary" round @click="dialogFormVisible = true">添加商品</el-button>
       </el-row>
        <el-tabs v-model="activeName" @tab-click="handleClick" type="card" closable @tab-remove="removeTab">
            <template v-for="item in foodType">
                <el-tab-pane 
                :label='item.foodTypeName' 
                :name="item.foodTypeId" 
                :key="item.foodTypeId">
                    <food-list 
                        :fooddata="foodsMap[item.foodTypeId]" 
                        @deletedclick="deleteds"
                        @setOfterclick="setOfterclick"
                        >
                    </food-list>
                </el-tab-pane>
            </template>
        </el-tabs>
        <!-- 添加分类 -->
        <el-dialog title="添加商品分类" :visible.sync="addFoodTypeVisible">
            <el-form :model="typeForm" ref="typeForm" :rules="typeRules">
                <el-form-item label="分类名称" :label-width="formLabelWidth" prop="foodTypeName">
                  <el-input v-model.trim="typeForm.foodTypeName" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFoodTypeVisible = false">取 消</el-button>
                <el-button type="primary" @click="addfoodType('typeForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!-- 添加商品 -->
        <el-dialog title="添加商品" :visible.sync="dialogFormVisible">
            <el-form :model="form" ref="form" :rules="rules">
                <el-form-item label="商品名称" :label-width="formLabelWidth" prop="foodsName">
                    <el-input v-model.trim="form.foodsName" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="单价" :label-width="formLabelWidth" prop="foodsPrice">
                    <el-input v-model.number="form.foodsPrice" autocomplete="off"></el-input>
                </el-form-item>
                <!-- <el-form-item label="数量" :label-width="formLabelWidth" prop="foodsCoun">
                    <el-input v-model.number="form.foodsCoun" autocomplete="off"></el-input>
                </el-form-item> -->
                <el-form-item label="常用" :label-width="formLabelWidth" prop="oftenFoods">
                    <el-switch v-model="form.oftenFoods">
                    </el-switch>
                </el-form-item>
                <el-form-item label="图片" :label-width="formLabelWidth" prop="foodsImg">
                    <!-- <el-upload  
                        list-type="picture-card"
                        action=""
                        :limit="1"
                        :show-file-list="false"
                        :http-request="handlePictureCardPreview"
                        :on-remove="handleRemove"
                        >
                        <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisible">
                        <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog> -->
                    <el-upload class="avatar-uploader" 
                    action="" 
                    :show-file-list="false"
                        :http-request="handlePictureCardPreview">
                        <img v-if="form.foodsImg" :src="form.foodsImg" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
                
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="addfoods('form')">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import foodList from '@/components/foodlist'
  export default {
    name:'commodity',
    data() {
      return {
         activeName: '',//选项卡当前项
         form: {
             foodsName: '',
             foodsPrice: 0,
            //  foodsCoun: 0,
             foodsImg: '',
             oftenFoods: 0,
        }, //新建商品
         formLabelWidth: '120px', //弹出框项
         dialogFormVisible: false,//弹出框项
         foodType: [],
        foodsMap: {},
        rules:{
            foodsName: [{ required: true, message: '请输入商品名称', trigger: 'blur' }],
            foodsPrice: [{ required: true, message: '请输入商品价格', trigger: 'blur' }],
            // foodsCoun: [{ required: true, message: '请输入商品数量', trigger: 'blur' }],
            foodsImg: [{ required: false, message: '请选择商品图片', trigger: 'blur' }],
        },
          dialogImageUrl:'',
          dialogVisible: false,
          addFoodTypeVisible: false,
          typeForm:{
              foodTypeName:''
          },
          typeRules:{
              foodTypeName: [{ required: true, message: '请输入商品分类', trigger: 'blur' }]
          }
      }
    },
    components: {
        foodList,
    },
    created () {
        this.getFoodsType()
    },
    methods: {
        getFoodsType() {
            // 食品分类
            this.$api.foodApi.getFoodsType({ user: this.$store.state.userAccount }, (res) => {
                if (res.code === 200) {
                    this.foodType = res.data
                    this.foodType.forEach(item=>{
                        item.foodTypeId += ''
                    })
                    res.data.map(item => item.foodTypeId).forEach((item)=>{
                        if (!this.foodsMap[item]){
                            this.$set(this.foodsMap, item + '', [])
                        }
                        // this.foodsMap[item] = []
                    })
                    let activeName = this.foodType[this.foodType.length - 1].foodTypeId + ''
                    this.getFoodsList(activeName)
                    this.activeName = activeName
                } else {
                    this.foodType = []
                }
            })
        },
        getFoodsList(type) {
            // 查询商品
            this.$api.foodApi.getFoodsList({type}, (res) => {
                if (res.code === 200) {
                    this.foodsMap[type] = res.data
                } else {
                    this.foodsMap[type] = []
                }
            })
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
            this.form.foodsImg = ''
        },
        handlePictureCardPreview(file) {
            var fileReader = new FileReader()
            fileReader.readAsDataURL(file.file)
            fileReader.onload = (e) => {
                this.dealImage(e.target.result,(val)=>{
                    this.form.foodsImg = val
                    console.log(this.form.foodsImg);
                }) //获取base64字符串
                // this.dialogImageUrl = e.target.result //获取base64字符串
            }
            // this.dialogImageUrl = URL.createObjectURL(file.raw);
            // this.dialogVisible = true;
        },
        dealImage(base64, callback) {
            // 压缩
            var newImage = new Image();
            var quality = 0.6;    //压缩系数0-1之间
            newImage.src = base64;
            newImage.setAttribute("crossOrigin", 'Anonymous');	//url为外域时需要
            var imgWidth, imgHeight;
            newImage.onload = function () {
                imgWidth = this.width;
                imgHeight = this.height;
                var canvas = document.createElement("canvas");
                var ctx = canvas.getContext("2d");
                if (Math.max(imgWidth, imgHeight) > 120) {
                    if (imgWidth > imgHeight) {
                        canvas.width = 120; // 设置压缩宽度大小
                        canvas.height = 120 * imgHeight / imgWidth;
                    } else {
                        canvas.height = 120;
                        canvas.width = 120 * imgWidth / imgHeight;
                    }
                } else {
                    canvas.width = imgWidth;
                    canvas.height = imgHeight;
                    quality = 0.6;
                }
                ctx.clearRect(0, 0, canvas.width, canvas.height);
                ctx.drawImage(this, 0, 0, canvas.width, canvas.height);
                var base64 = canvas.toDataURL("image/jpeg", quality); //压缩语句
                // 如想确保图片压缩到自己想要的尺寸,如要求在5-10kb之间，请加以下语句，quality初始值根据情况自定
                while (base64.length / 1024 > 10) {
                    quality -= 0.01;
                    base64 = canvas.toDataURL("image/jpeg", quality);
                }
                // 防止最后一次压缩低于最低尺寸，只要quality递减合理，无需考虑
                while (base64.length / 1024 < 5) {
                    quality += 0.001;
                    base64 = canvas.toDataURL("image/jpeg", quality);
                }
                callback (base64);//必须通过回调函数返回，否则无法及时拿到该值
            }
        },
        handleClick(tab, event) {
        // console.log(tab, event);
            this.getFoodsList(tab.name)
        },
        removeTab(targetName) {
            this.$confirm('是否确定删除该分类及其所属商品', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$api.foodApi.deleteFoodType({ id: targetName }, (res) => {
                    if (res.code === 200) {
                        this.getFoodsType()
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }else{
                        this.$message({
                            type: 'error',
                            message: res.msg
                        });
                    }
                })
            
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        deleteds (val){
            // console.log(val);
            this.$confirm('是否确定删除该商品', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$api.foodApi.deleteFoods({ id: val }, (res) => {
                    if (res.code === 200) {
                        this.getFoodsList(this.activeName)
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    } else {
                        this.$message({
                            type: 'error',
                            message: res.msg
                        });
                    }
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        setOfterclick (val){
            // console.log(val);
            this.$api.foodApi.setOftenFoods({ id: val.id, oftenFoods: val.oftenFoods === '0'?'1':'0'}, (res) => {
                if (res.code === 200) {
                    this.getFoodsList(this.activeName)
                    this.$message({
                        type: 'success',
                        message: '设置成功!'
                    });
                } else {
                    this.$message({
                        type: 'error',
                        message: res.msg
                    });
                }
            })
        },
        addfoods (formName){
            // 新增商品
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    let params = { ...this.form }
                    params.oftenFoods = params.oftenFoods ? 1 : 0
                    params.foodTypeId = Number(this.activeName)
                    params.user = this.$store.state.userAccount
                    this.$api.foodApi.addFoodsDetail(params, (res) => {
                        if (res.code === 200) {
                            // this.foodsMap[this.activeName].push({ ...this.form })
                            this.getFoodsList(this.activeName)
                            this.$message({
                                message: '添加成功',
                                type: 'success',
                            })
                            this.$refs[formName].resetFields()
                            this.dialogFormVisible = false
                        } else {
                            this.$message({
                                message: res.msg,
                                type: 'error',
                            })
                        }
                    })
                }
            })
        },
        addfoodType (formName) {
            // 新增商品分类
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$api.foodApi.addFoodsType(
                        { foodTypeName: this.typeForm.foodTypeName,
                            user: this.$store.state.userAccount
                         },
                         (res) => {
                        if (res.code === 200) {
                            this.getFoodsType(this.activeName)
                            this.$message({
                                message: '添加成功',
                                type: 'success',
                            })
                            this.$refs[formName].resetFields()
                            this.addFoodTypeVisible = false
                        } else {
                            this.$message({
                                message: res.msg,
                                type: 'error',
                            })
                        }
                    })
                }
            })
        }
    }
}
</script>

<style scoped>
.el-row{
    position: fixed;
    top: 3px;
    right: 0;
    z-index: 999;
}
.el-row button{
    float: right;
    margin-right: 20px; 
}
.el-button.is-round{
    padding: 8px 23px;
}
.el-tabs__nav-scroll{
    margin-left: 10px;
}
</style>


