import * as http from '@/api/http.js'

const foodApi = {
    // 查询食品分类
    getFoodsType(data,callBack,error){
        return http.get('/food/getFoodsType',data).then(callBack).catch(error)
    },
    // 根据食品分类查询商品
    getFoodsList(data, callBack, error) {
        return http.get('/food/getFoodsList', data).then(callBack).catch(error)
    },
    // 新增食品分类
    addFoodsType(data, callBack, error) {
        return http.post('/food/addFoodsType', data).then(callBack).catch(error)
    },
    // 新增商品
    addFoodsDetail(data, callBack, error) {
        return http.post('/food/addFoodsDetail', data).then(callBack).catch(error)
    },
    // 删除食品分类
    deleteFoodType(data, callBack, error) {
        return http.post('/food/deleteFoodType', data).then(callBack).catch(error)
    },
    // 删除商品
    deleteFoods(data, callBack, error) {
        return http.post('/food/deleteFoods', data).then(callBack).catch(error)
    },
    // 设置常用商品
    setOftenFoods(data, callBack, error) {
        return http.post('/food/setOftenFoods', data).then(callBack).catch(error)
    },
    // 查询常用商品
    getOftenFoods(data, callBack, error) {
        return http.get('/food/getOftenFoods', data).then(callBack).catch(error)
    },
    // 查询所有购买记录
    getFoodsAllList(data, callBack, error) {
        return http.get('/food/getFoodsAllList', data).then(callBack).catch(error)
    },
    // 查询时间范围VIP所有购买记录
    getFoodsVIPAllList(data, callBack, error) {
        return http.get('/food/getFoodsVIPAllList', data).then(callBack).catch(error)
    },
}

export default foodApi


