<template>
    <div>
        <el-row>
            <el-col :span="10" :offset="1">
                <!-- 商品销量（近一月） -->
                <div id="foodsTop" style="width: 100%;height:50vh;"></div>
            </el-col>
            <el-col :span="10" :offset="1">
                <!-- 商品销量（近三月） -->
                <div id="foodsTopJidu" style="width: 100%;height:50vh;"></div>
            </el-col>
            <el-col :span="10" :offset="1">
                <!-- vip常购（近一月） -->
                <div id="vipFoods" style="width: 100%;height:50vh;"></div>
            </el-col>
            <el-col :span="10" :offset="1">
                <!-- 新增vip数量（近一年） -->
                <div id="vipNum" style="width: 100%;height:50vh;"></div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
// 引入 ECharts 主模块
var echarts = require('echarts/lib/echarts');
// 引入柱状图
require('echarts/lib/chart/bar');
// 引入提示框和标题组件
require('echarts/lib/component/tooltip');
require('echarts/lib/component/title');
export default {
    name:'statustics',
    data(){
        return{
            
        }
    },
    created(){
    },
    mounted:function(){
        this.getFoodslist()
        this.getFoodsviplist()
        this.getAddVipList()
    },
    methods:{
        getFoodslist() {
            this.$api.foodApi.getFoodsAllList({ 
                endTime: (new Date().getTime() - 1000 * 60 * 60 * 24 * 90 ),
                user: this.$store.state.userAccount 
            }, (res) => {
                if (res.code === 200) {
                    let currData = new Date().getTime(), food1 = [], food2 = [];
                    res.data.sort((a, b) => b.times - a.times)
                    res.data.map(item => {
                        item.foodlist = JSON.parse(item.foodlist)
                        // 商品销量（近一月）
                        if (currData - item.times < 1000 * 60 * 60 * 24 * 30) {
                            this.filterFoods(item,food1)
                        }
                        // 近三月
                        if (currData - item.times < 1000 * 60 * 60 * 24 * 90) {
                            this.filterFoods(item, food2)
                        }
                    })
                    console.log(food1,food2);
                    this.initEchart(food1,'foodsTop','近一月热销商品')
                    this.initEchart(food2,'foodsTopJidu','近三月热销商品')
                }
            })
        },
        getFoodsviplist() {
            this.$api.foodApi.getFoodsVIPAllList({ 
                endTime: (new Date().getTime() - 1000 * 60 * 60 * 24 * 30 ),
                user: this.$store.state.userAccount 
            }, (res) => {
                if (res.code === 200) {
                    let vipfood = [];
                    res.data.sort((a, b) => b.times - a.times)
                    res.data.map(item => {
                        item.foodlist = JSON.parse(item.foodlist)
                        this.filterFoods(item, vipfood)
                    })
                    this.initEchart(vipfood,'vipFoods','VIP常购商品')
                }
            })
        },
        getAddVipList() {
            let currYear = new Date().getFullYear()
            let endTime = new Date(currYear + '-01-01 0:0:0').getTime()
            this.$api.vipApi.getVipList({ endTime, user: this.$store.state.userAccount }, (res) => {
                if (res.code === 200) {
                    let data = []
                    res.data.forEach(item=>{
                        let obj = data.find(item1 => item1.times === item.times)
                        if (obj) {
                            obj.count += 1
                        } else {
                            obj = {
                                times: item.times,
                                count: 1,
                            }
                            data.push(obj)
                        }
                    })
                    data.sort((a,b)=>a.times - b.times)
                    // 基于准备好的dom，初始化echarts实例
                    var myChart = echarts.init(document.getElementById('vipNum'));
                    // 绘制图表
                    myChart.setOption({
                        title: {
                            text: '新增vip数量（近一年）'
                        },
                        tooltip: {},
                        xAxis: {
                            data: data.map(item => this.filterTime(item.times))
                        },
                        yAxis: {},
                        series: [{
                            name: '新增',
                            type: 'bar',
                            data: data.map(item => item.count)
                        }]
                    });
                }
            })
        },
        filterFoods(item,arr){
            item.foodlist.forEach(food => {
                let obj = arr.find(item1 => item1.foodsId === food.foodsId)
                if (obj) {
                    obj.count += food.count
                } else {
                    obj = {
                        count: food.count,
                        foodsName: food.foodsName,
                        foodsId: food.foodsId
                    }
                    arr.push(obj)
                }
            });
            // return arr
        },
        initEchart(data,dom,title) {
            data = data.sort((a, b) => b.count - a.count).splice(0,5)
            // 基于准备好的dom，初始化echarts实例
            var myChart = echarts.init(document.getElementById(dom));
            // 绘制图表
            myChart.setOption({
                title: {
                    text: title
                },
                tooltip: {},
                xAxis: {
                    data: data.map(item => item.foodsName)
                },
                yAxis: {},
                series: [{
                    name: '数量',
                    type: 'bar',
                    data: data.map(item => item.count)
                }]
            });
        },
        filterTime (val) {
            let date = new Date(Number(val))
            return ((date.getMonth() + 1) + '-' + date.getDate())
        }
    }
}
</script>

<style scoped>

</style>


