const mutations = {// 这里面的参数除了state之外还传了需要增加的值sum，调用this.$store.commit('newNum',sum)
    setUserName(state, newVal) {
        state.userName = newVal
    },
    setUserAccount(state, newVal) {
        state.userAccount = newVal
    }
}
export default mutations



