<template>
  <div :class="meunArr.length ? 'left-nav' : 'defa-nav'">
    <ul style="position: relative">
      <li v-for="item in meunArr" :key="item.name">
        <router-link :to="item.to">
          <i :class="item.icon"></i>
          <div>{{ item.name }}</div>
        </router-link>
      </li>
      <li @mouseover="showHeaderUser" @mouseleave="hideHeaderUser" v-if="user" >
        <el-tooltip :content="user.name" placement="top" effect="light">
          <el-avatar :src="user.userImg" :size="60"></el-avatar>
        </el-tooltip>
        <transition name="slide-fade">
          <div class="headerUser" v-show="headerUser">
            <span @click="loginOut">退出</span>
          </div>
        </transition>
      </li>
    </ul>
  </div>
</template>
<script>
import meuns from '../common/common.js'
export default {
  name: 'leftNav',
  data() {
    return {
      meunArr: [],
      user: null,
      headerUser: false
    }
  },
  created() {
    if (localStorage.getItem('pos_user')) {
      this.user = JSON.parse(localStorage.getItem('pos_user'))
      this.meunArr = meuns.meunArr
    }
    window.addEventListener('setItem', () => {
      this.user = JSON.parse(localStorage.getItem('pos_user'))
      this.meunArr = meuns.meunArr
    })
  },
  methods: {
    showHeaderUser() {
      if (this.user) {
        this.headerUser = true
      }
    },
    hideHeaderUser() {
      this.headerUser = false
    },
    loginOut() {
      localStorage.clear()
      this.$router.go()
    }
  }
}
</script>
<style scoped>
ul {
  height: 100vh;
}

.defa-nav {
  color: #000;
  font-size: 10px;
  background-color: #EFF2F7;
  float: left;
  width: 80px;
}

.left-nav {
  color: #fff;
  font-size: 14px;
  background-color: #1d8ce0;
  float: left;
  width: 80px;
}

.iconfont {
  font-size: 24px;
}

.left-nav ul {
  padding: 0;
  margin: 0;
  background: inherit;
}

.left-nav li {
  list-style: "none";
  text-align: center;
  border-bottom: 1px solid inherit;
  padding: 10px;
  background: inherit;
}

ul li:last-of-type {
  position: absolute;
  bottom: 100px;
  left: 0;
  padding: 0 10px;
}

ul li:last-of-type::marker {
  content: '';
}

a {
  display: block;
  color: #fff;
  text-decoration: none;
}

.headerUser {
  height: 60px;
  width: 100px;
  position: absolute;
  bottom: 0px;
  left: 70px;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  background: inherit;
  color: inherit;
  border-radius: 0 30px 30px 0;
}

.headerUser span {
  padding: 10px;
  cursor: pointer;
}
</style>



