<template>
    <div>
        <el-row>
            <el-col :span='16' class='pos-order' id='orders'>
                <el-row>
                    <el-col :span='20' :offset="2">
                        <div v-if="currVip">
                            <p>客户名称：{{ currVip.vipName }}</p>
                            <p>客户编号：{{ currVip.vipId }}</p>
                            <p>加入日期：{{ currVip.times }}</p>
                            <p>购买记录：</p>
                        </div>
                        <div id="goumaijilv">
                            <el-collapse v-model="activeName" accordion>
                                <el-collapse-item v-for="(value,key,index) in currVip.foodObj" 
                                :key="index" :title="key" :name="index+1">
                                <ul>
                                        <li v-for="(item1,index1) in value[0].foodlist" :key="index1">
                                            <span>{{ item1.foodsName }}</span>
                                            <span>{{ item1.count }}</span>
                                            <span>￥{{ item1.foodsPrice }}</span>
                                        </li>
                                        <li>
                                            <span>&nbsp;</span>
                                            <span>&nbsp;</span>
                                            <span style="color:red">总计：￥{{value[0].price}}</span>
                                        </li>
                                    </ul>
                                </el-collapse-item>
                            </el-collapse> 
                        </div>
                        <div id="vipecharts" style="height:300px"></div>
                    </el-col>
                </el-row>
            </el-col>
            <el-col :span='8' class='pos-order' id='order'>
                 <el-row>
                     <el-col :span='12'>
                        <el-input v-model="searchinput"></el-input>
                     </el-col>
                     <el-col :span='6'>
                         <el-button style="opacity:0" type="primary" round>搜索</el-button>
                     </el-col>
                     <el-col :span='6'>
                        <el-button type="primary" round @click="vipbox">添加</el-button>
                     </el-col>
                </el-row>
                <div id="viplistbox">
                    <div v-for="(item,index) in currVipsData" :key="index" >
                        <div @click="xiangqin(item)">
                            <el-card shadow="hover">
                                <p>会员名：{{ item.vipName }}</p>
                                <p>加入日期：{{ item.times | filterTime }}</p>
                                <p>会员编号：{{ item.vipId }}</p>
                            </el-card>
                        </div>
                    </div>
                </div>
            </el-col>
        </el-row>
        <!-- 添加 -->
        <el-dialog title="新增会员" :visible.sync="dialogFormVisible">
        <el-form :model="form">
            <el-form-item label="会员名" :label-width="formLabelWidth">
                <el-input v-model="form.vipName" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="会员编号" :label-width="formLabelWidth">
                <el-input v-model="form.vipId" autocomplete="off" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="加入日期" :label-width="formLabelWidth">
                <el-input v-model="form.times" autocomplete="off" :disabled="true"></el-input>
            </el-form-item>
            
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="addvips">确 定</el-button>
        </div>
        </el-dialog>
    </div>
</template>

<script>
// 引入 ECharts 主模块
var echarts = require('echarts/lib/echarts');
// 引入柱状图
require('echarts/lib/chart/bar');
// 引入提示框和标题组件
require('echarts/lib/component/tooltip');
require('echarts/lib/component/title');
export default {
    name:'member',
    data(){
        return{
            form:{
                times: null,
                vipName: '',
                vipId: null,
                user: this.$store.state.userAccount
            },//vip新建数据
            dialogFormVisible:false,//弹窗所需项
            searchinput:'',//搜索项
            formLabelWidth:'120px',//弹窗所需项
            vipsData:[],//vip数据列表
            activeName:'1',//手风琴当前项
            currVip:{},
        }
    },
    computed:{
        currVipsData(){
            if(this.searchinput){
                return this.vipsData.filter(item => item.vipId.indexOf(this.searchinput) > -1)
            }else{
                return this.vipsData
            }
        }
    },
    created(){
        this.getVipList()
    },
    mounted: function () {
        var orderHeight = document.body.clientHeight
        document.querySelector('#order').style.height = orderHeight + 'px'
        document.querySelector('#orders').style.height = orderHeight + 'px'
        document.querySelector('#viplistbox').style.height = (orderHeight - 60) + 'px'
        document.querySelector('#goumaijilv').style.maxHeight = (orderHeight - 376) + 'px';
    },
    methods:{
        getVipList () {
            this.$api.vipApi.getVipList({ user:this.$store.state.userAccount },(res)=>{
                this.vipsData = []
                if(res.code === 200){
                    this.vipsData = res.data.sort((a,b)=>b.times -a.times)
                    this.xiangqin(res.data[0])
                }
            })
        },
        addvips(){
            this.form.times = new Date(this.form.times).getTime()
            this.$api.vipApi.addVipUser(this.form, (res) => {
                if (res.code === 200) {
                    this.getVipList();
                    this.dialogFormVisible = false;
                    this.form = {
                        times: null,
                        vipName: '',
                        vipId: null,
                        user: this.$store.state.userAccount
                    }
                }else{
                    this.$message({
                        message: '添加会员失败',
                        type: 'warning',
                    })
                }
            })
        },
        vipbox(){
            let currdate = new Date();
            this.form.times = currdate.getFullYear() + '-' + (currdate.getMonth() + 1) + '-' + currdate.getDate();
            this.form.vipId = currdate.getFullYear() + '' + currdate.getTime();
            this.dialogFormVisible = true;
        },
        xiangqin(item){
            this.currVip = {...item};
            this.$api.vipApi.getVipFoodsList({ vipId:item.vipId }, (res) => {
                if (res.code === 200) {
                    let tiemList = [], foodObj = {},currData = new Date().getTime(),echarts = [];
                    res.data.sort((a,b)=>b.times - a.times)
                    res.data.map(item=>{
                        let tims = new Date(Number(item.times))
                        let currdatekey = tims.getFullYear() + '-' + (tims.getMonth() + 1) + '-' + tims.getDate()
                            + ' ' + tims.getHours() + ':' + tims.getMinutes() + ':' + tims.getSeconds();
                        console.log(currdatekey);
                        item.foodlist = JSON.parse(item.foodlist)
                        if(!tiemList.includes(item.times)){
                            tiemList.push(item.times)
                            foodObj[currdatekey] = []
                        }
                        foodObj[currdatekey].push(item)
                        // echarts
                        if (currData - item.times < 1000 * 60 * 60 * 24 * 30){
                            item.foodlist.forEach(food => {
                                let obj = echarts.find(item1 => item1.foodsId === food.foodsId)
                                if (obj){
                                    obj.count += food.count
                                }else{
                                    obj = {
                                        count: food.count,
                                        foodsName: food.foodsName,
                                        foodsId: food.foodsId
                                    }
                                    echarts.push(obj)
                                }
                            });
                        }
                    })
                    console.log(echarts);
                    this.initEchart(echarts)
                    this.$set(this.currVip, 'foodObj', foodObj)
                    console.log(this.currVip);
                }
            })
        },
        initEchart(echart) {
            echart = echart.sort((a, b) => b.count - a.count).splice(0, 5)
            // 基于准备好的dom，初始化echarts实例
            var myChart = echarts.init(document.getElementById('vipecharts'));
            // 绘制图表
            myChart.setOption({
                title: {
                    text: '近一月常购商品'
                },
                tooltip: {},
                xAxis: {
                    data: echart.map(item=>item.foodsName)
                },
                yAxis: {},
                series: [{
                    name: '数量',
                    type: 'bar',
                    data: echart.map(item => item.count)
                }]
            });
        }
    },
    filters:{
        filterTime(val){
            let date = new Date(Number(val))
            return (date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate())
        }
    }
}
</script>

<style scoped>
#order{
    padding-top: 10px;
}

.el-card{
    margin-top: 10px;
    background:#e4e2d7;
    cursor: pointer;
}
p{
    text-align: left;
    overflow: hidden;
}
#viplistbox,#goumaijilv{
    overflow: auto;
}
#goumaijilv ul{
    padding: 0 10px;
}
#goumaijilv li{
    line-height: 30px;
    border-bottom: 1px solid #ddd;
    list-style-type: none;
}
#goumaijilv li span{
    display: inline-block;
    width: 30%;
    text-align: left;
}
#goumaijilv >>> .el-collapse-item__header{
    padding-left: 15px;
}
</style>


