const actions = {// 自定义触发mutations里函数的方法，context与store 实例具有相同方法和属性,调用this.$store.dispatch('hideFooter')
    setUserName(context) {
        context.commit('setUserName')
    },
    setUserAccount(context) {
        context.commit('setUserAccount')
    }
}
export default actions



